import React from "react";

export default class ProductDetailsNavigationComponent extends React.Component {

    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this._loadModules();
    }

    render() {
    console.log("*******************************************");
    console.log(this.props.deviceData);

        return (
            <div className="u-small-hide u-spacing-xl js-stop-sticking u-padding-top-l">
            	<div id={'opl-scroll-to-element'} data-js-module={'core/modules/opl-scroll-to-element'} className={this.props.noShadow?"opl-wiking-product-menu u-spacing-xl u-no-shadow":"opl-wiking-product-menu u-spacing-xl "} >
            		<div className="l-page-row">
            			<ul>
                            {this.createPromotionLi()}
            				{this.props.deviceData.description?<li className={!this.props.deviceData.promotion ? "active" : ""}><a href="#product-description" className="opl-scroll-to-element-item">Opis</a></li> : null}
            			  	{this.props.deviceData.classification && this.props.deviceData.classification.length > 0?<li><a href="#product-details" className="opl-scroll-to-element-item">Specyfikacja</a></li> : null}
                            {this._createRecommendedProductsLi()}
            				<li><a href="#product-comments" className="opl-scroll-to-element-item">Recenzje</a></li>
                            <li><a href="#product-delivery" className="opl-scroll-to-element-item">Dostawa i płatność</a></li>
            			</ul>
            		</div>
            	</div>
            </div>
        )
    }

    createPromotionLi() {
        if (!(this.props.deviceData.promotion === 0 || this.props.deviceData.promotion == null || !this.props.deviceData.promotion)) {
            return <li className="active"><a href="#product-promotion" className="opl-scroll-to-element-item">{this.props.promotionNavigationLabel ? this.props.promotionNavigationLabel : ''}</a></li>;
        }
        return null;
    }

    _createRecommendedProductsLi() {
        if (!(this.props.recommendedProducts.length === 0 || this.props.recommendedProducts == null || !this.props.recommendedProducts)) {
            return <li><a href={"#product-recommended-anchor" + this.props.deviceData.productId} className="opl-scroll-to-element-item">{this.props.recommendedProductsLabel ? this.props.recommendedProductsLabel : ''}</a></li>;
        }
        return null;
    }

    _loadModules() {
        OPL.UI.loadModules(document.getElementById("opl-scroll-to-element"), [{
            path: 'common/modules/opl-scroll-to-element',
            options: ''
        }]);
    }
}